<template>
    <div class="dialog">
        <el-dialog
            :title="dialog.title"
            :visible.sync="dialog.show"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
            :modal-append-to-body="false"
        >
            <div class="form">
                <el-form
                    ref="form"
                    :model="formData"
                    :rules="form_rules"
                    label-width="120px"
                    style="margin:10px;width:auto"
                >
                    <el-form-item label=":">
                        <el-select v-model="formData.type" placeholder="">
                            <el-option v-for="(formtype,index) in format_type_list"
                                :key="index" :value="formtype" :label="formtype"
                            >
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item prop="describe" label=":">
                        <el-input type="describe" v-model="formData.describe"></el-input>
                    </el-form-item>
                    <el-form-item prop="income" label=":">
                        <el-input type="income" v-model="formData.income"></el-input>
                    </el-form-item>
                    <el-form-item prop="expend" label=":">
                        <el-input type="expend" v-model="formData.expend"></el-input>
                    </el-form-item>
                    <el-form-item prop="cash" label=":">
                        <el-input type="cash" v-model="formData.cash"></el-input>
                    </el-form-item>
                    <el-form-item prop="remark" label=":">
                        <el-input type="textarea" v-model="formData.remark"></el-input>
                    </el-form-item>
                    <el-form-item class="text_right">
                        <el-button @click="dialog.show = false"></el-button>
                        <el-button type="primary" @click="onSubmit('form')"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name:'dialog',
        data(){
            return{
                format_type_list:[
                    '',
                    '',
                    '',
                    '',
                    '',
                    ''
                ],
                form_rules:{
                    describe:[
                        {required:true,message:"!",trigger:'blur'}
                    ],
                    income:[
                        {required:true,message:"!",trigger:'blur'}
                    ],
                    expend:[
                        {required:true,message:"!",trigger:'blur'}
                    ],
                    cash:[
                        {required:true,message:"!",trigger:'blur'}
                    ]
                }
            }
        },
        props:{
            dialog:Object,
            formData:Object
        },
        methods: {
            onSubmit(form){
                this.$refs[form].validate(validate=>{
                    if(validate){
                        const url = this.dialog.option === 'add'? 'add':`edit/${this.formData.id}`
                        this.$axios.post(`/api/profiles/${url}`,this.formData)
                            .then(res=>{
                                this.$message({
                                    message:'',
                                    type:'success'
                                })
                                //  dialog
                                this.dialog.show = false
                                this.$emit('update')
                            })
                    }
                })
            }
        },
    }
</script>

<style lang='scss' scoped>

</style>
